<template>
  <div id="javascript" style="height: 88vh;">
    <scroll class="wrapper" :data="data" :pulldown="pulldown" @pulldown="loadData">
      <item :item="data"></item>
    </scroll>
  </div>
</template>

<script>
  import data1 from '../utils/mock1';
  import scroll from './scroll.vue';
  export default {
    data () {
      return {
        items: [],
        data: [],
        pulldown: true
      }
    },
    created() {
        this.loadData();
    },
    methods: {
        loadData() {
          this.$http.get('http://g.cn').then((data) => {
                this.data =  data.body.res;
            })
        }
    }
  }

</script>

<style lang="scss" scoped>

</style>



