<template>
  <div id="vue">
   <div class="map" @click="map">获取地理位置</div>
   <item :item="items"></item>

  </div>
</template>

<script>
  export default {
    data () {
          return {
          items: []
      }
    },
    mounted: function () {
      this.$http.get('../../static/vue.json').then(function (res){
        if(!res.ok) return;
        this.items = res.body;
      }).catch(function (err){
        console.log(err);
      })
    },
    methods:{
        map(){
            console.log(111);
            this.$http.get('https://api.map.baidu.com/geocoder/v2/?address=广东省深圳市龙岗区龙岗街道硅谷动力电子商务港&output=json&ak=laFPrw0xhuy2GoG4UPHF0SXSMKYeFg8u&callback=showLocation').then(function(res){
                console.log(res);
            })
        }
    }
  }
</script>

<style lang="scss" scoped>
  .item{
    margin-bottom:20px;
    .title{
      color: #9ec09f;
      font-size: 22px;
      font-family: 微软雅黑;
      line-height: 22px;
    }
    img{
      width: 95%;
      margin: 20px 0 10px;
    }
    .time{
      color: #a0a0a0;
      font-size: 14px;
      line-height: 14px;
      margin: 0 0 10px 0;
    }
    .describe{
      color: #3f3f3f;
      font-size: 14px;
      width: 100%;
    }

  }

</style>



